import React from 'react'
import Box from '@mui/material/Box';

const Contact = () => {
  return (
    <div>
      <Box paddingTop='70px'>Contact</Box>
      <Box textAlign= 'center' paddingTop='100px'>Is the app not working? Would you like to give feedback or suggestions for features?</Box>
      <Box textAlign= 'center' paddingTop='120px'>Then fill up the form below to let us know. We welcome any feedback!</Box>
      <Box textAlign= 'center' paddingTop='140px'>(form)</Box>
    </div>
  )
}

export default Contact