//table to display student's results to teachers
import React from 'react'
import Box from '@mui/material/Box';
import Upload from './Upload';

const Table = () => {
  return (
    <div>
      <Box textAlign= 'center' paddingTop='80px'>This section is only visible to teachers</Box>
      <Box textAlign= 'center' paddingTop='100px'>(Enter password and then Table of student results appears)</Box>
      <Upload/>
    </div>
  )
}

export default Table