import React from 'react'
import Box from '@mui/material/Box';
import Password from './components/Password';

const Exam = () => {
  return (
    <div>
      <Box textAlign= 'center' paddingTop='80px'>EE Exam</Box>

      <Box textAlign= 'center' paddingTop='150px'>Date: 1st December 2023, Time: 1:00pm</Box>

      <Box textAlign= 'center' paddingTop='200px'>Please read all the instructions carefully. 
      Do not start the exam until you are told to do so. This exam contains 3 multiple-choice questions. 
      If you have any queries at any point of the examination, please alert the examiners. 
      This is a 30 minutes paper.
      </Box>
      <Box textAlign= 'center' paddingTop='210px'>(put a password text box and then once student enters password correctly, it will take them to exam page)
      </Box>
      <Password/>
    </div>
  )
}

export default Exam