import React from 'react'
import Box from '@mui/material/Box';

const Home = () => {
  return (
    <div>
      <Box textAlign= 'center' paddingTop='100px'>Welcome to (insert app name)! The one stop app for all your exam related needs!</Box>

      <Box textAlign= 'center' paddingTop='200px'>To access your exams and other features, please use the hamburger menu.</Box>
    </div>
  )
}

export default Home